    </transition>
</template>
<script>
    export default {
        name: 'Navbar',
        inject: ['eventBus'],
        data() { return { selectedIndex: '1', visible: false, smallSize: false } },
        mounted() {
            //监听其他组件的跳转指令，并更新导航栏的状态
            this.eventBus.$on('link-to', this.listenLinkTo)
            //监听滚动行为导致页面展示的component改变，更新导航的展开和激活
            this.eventBus.$on('instage-component', this.listenSroll)
            //监听header的menu图标被点击
            this.eventBus.$on('click-menu', this.clickMenu)
            window.location.href && window.location.href.match(/^.+\/#\/(.*)$/)
            let path = RegExp.$1
            if (path) {
                this.$nextTick(() => {
                    this.$refs.menu.updateMenu({ path })
                })
            }
            this.$nextTick(() => {
                //检查屏幕大小确定是否开启小屏幕模式，并添加监听
                this.listenWindow()
                window.addEventListener('resize', this.listenWindow)
            })
        },
        methods: {
            listenLinkTo(path) {
                this.$router.push('/' + path)
                this.$nextTick(() => {
                    this.$refs.menu.updateMenu({ path })
                })
            },
            pathChange(path) {
                this.$router.push('/' + path)
                path.match(/^\w+\/\w+\/(\w+)$/)
                let name = RegExp.$1
                if (name) {
                    this.eventBus.$emit('link-component', name)
                }
            },
            listenSroll(path) {
                //防止在点击item时重复调用updateMenu
                if (this.$route.path === '/' + path) { return }
                this.$router.push('/' + path)
                this.$nextTick(() => {
                    this.$refs.menu.updateMenu({ path })
                })
            },
            clickMenu() {
                this.visible = !this.visible
            },
            listenWindow() {
                //根据屏幕大小确定模式，small时默认导航栏隐藏
                let width = document.documentElement.clientWidth || document.body.clientWidth
                if (width < 800) {
                    this.smallSize = true
                    this.visible = false
                } else {
                    this.smallSize = false
                    this.visible = true
                }
            }
        },
        watch: {
            $route: {
                //屏幕宽度较小path变化后关闭导航栏
                handler(val, oldVal) {
                    if (val.path !== oldVal.path) {
                        this.smallSize ? this.visible = false : ''
                    }
                },
                deep: true
            }
        },
        beforeDestroy() {
            window.removeEventListener('resize', this.listenWindow)
            this.eventBus.$off('in-stage-component', this.listenSroll)
            this.eventBus.$off('link-to', this.listenLinkTo)
            this.eventBus.$off('click-menu', this.clickMenu)
        }
    }
</script>
